<template>
  <div class="sidebar">
    <el-menu
      class="sidebar-el-menu"
      :default-active="onRoutes"
      :collapse="collapse"
      background-color="#324157"
      text-color="#bfcbd9"
      active-text-color="#20a0ff"
      unique-opened
      router
    >
      <template v-for="item in items">
        <template v-if="item.subs">
          <el-submenu :index="item.index" :key="item.index">
            <template slot="title">
              <i :class="item.icon"></i>
              <span slot="title">{{ item.title }}</span>
            </template>
            <template v-for="subItem in item.subs">
              <el-submenu
                v-if="subItem.subs"
                :index="subItem.index"
                :key="subItem.index"
              >
                <template slot="title">{{ subItem.title }}</template>
                <el-menu-item
                  v-for="(threeItem,i) in subItem.subs"
                  :key="i"
                  :index="threeItem.index"
                >{{ threeItem.title }}
                </el-menu-item>
              </el-submenu>
              <el-menu-item
                v-else
                :index="subItem.index"
                :key="subItem.index"
              >{{ subItem.title }}
              </el-menu-item>
            </template>
          </el-submenu>
        </template>
        <template v-else>
          <el-menu-item :index="item.index" :key="item.index">
            <i :class="item.icon"></i>
            <span slot="title">{{ item.title }}</span>
          </el-menu-item>
        </template>
      </template>
    </el-menu>
  </div>
</template>

<script>
import bus from '../common/bus'

export default {
  data () {
    return {
      collapse: false,
      items: [
        {
          icon: 'el-icon-rank',
          index: 'introduction',
          title: '系统首页'
        },
        {
          icon: 'el-icon-rank',
          index: '3',
          title: '集成算法界面',
          subs: [
            {
              index: 'HiSeekerPage',
              title: 'HiSeeker算法'
            },
            {
              index: 'ClusterMIPage',
              title: 'ClusterMI算法'
            },
            {
              index: 'DCHEPage',
              title: 'DCHE算法'
            },
            {
              index: 'DECMDRPage',
              title: 'DECMDR算法'
            },
            {
              index: 'MACOEDPage',
              title: 'MACOED算法'
            },
            {
              index: 'DualWMDRPage',
              title: 'DualWMDR算法'
            },
            {
              index: 'MOMDRPage',
              title: 'MOMDRPage算法'
            },
            {
              index: 'upload8',
              title: '算法8'
            }
          ]
        },
        {
          icon: 'el-icon-rank',
          index: 'DataGeneration',
          title: '数据生成界面'
        },
        {
          icon: 'el-icon-rank',
          index: 'MethodComparison',
          title: '算法对比界面'
        },
        {
          icon: 'el-icon-rank',
          index: 'SNPInquery',
          title: 'SNP查询界面'
        },
        {
          icon: 'el-icon-rank',
          index: 'TaskSummary',
          title: '任务控制界面'
        }
      ]
    }
  },
  computed: {
    onRoutes () {
      return this.$route.path.replace('/', '')
    }
  },
  created () {
    // 通过 Event Bus 进行组件间通信，来折叠侧边栏
    bus.$on('collapse', msg => {
      this.collapse = msg
      bus.$emit('collapse-content', msg)
    })
  }
}
</script>

<style scoped>
.sidebar {
  display: block;
  position: absolute;
  left: 0;
  top: 70px;
  bottom: 0;
  overflow-y: scroll;
}

.sidebar::-webkit-scrollbar {
  width: 0;
}

.sidebar-el-menu:not(.el-menu--collapse) {
  width: 250px;
}

.sidebar > ul {
  height: 100%;
}
</style>



